form {
  width: 40vw;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.searchBox input {
  width: 70%;
  line-height: 40px;
  background: #fff;
  border: none;
  padding-left: 15px;
}

.searchBtn {
  color: #fff;
  background: #01e6ef;
  border: 1px solid #01e6ef;
  font-size: 22px;
  width: 4vw;
  height: 100%;
  cursor: pointer;
}

.searchBtn a {
  width: 100%;
}

@media only screen and (max-width: 992px) {
  .searchBtn {
    width: 6vw;
  }
}

@media only screen and (max-width: 600px) {
  .searchBox input {
    min-width: 70vw;
  }
  .searchBtn {
    width: 10vw;
  }
}



